/* Checkboxes and Radios */
.form-check,
.form-radio {
	position: relative;
	display: block;
	margin-top: 15px;
	margin-bottom: 10px;
}

.form-check .form-check-label,
.form-radio .form-check-label {
	display: block;
	padding-left: 30px;
	line-height: 1.5;
}

.rtl .form-check .form-check-label,
.rtl .form-radio .form-check-label {
	padding-left: 0;
	padding-right: 30px;
}

.form-check .form-check-label input,
.form-radio .form-check-label input {
	position: absolute;
	margin-left: -20px;
	margin-top: 4px\9;
	top: 0;
	left: 0;
	z-index: 1;
	cursor: pointer;
	opacity: 0;
	filter: alpha(opacity=0);
	margin-top: 0;
}

.rtl .form-check .form-check-label input,
.rtl .form-radio .form-check-label input {
	left: auto;
	right: 0;
}

.form-check {
	padding-left: 0;
}

.form-check label {
	font-size: 0.875rem;
	line-height: 1.5;
}

.form-check label input:checked + .input-helper:before {
	background-color: #ffffff;
}

.form-check label input:checked + .input-helper:after {
	width: 18px;
	opacity: 1;
	line-height: 18px;
	filter: alpha(opacity=100);
	transform: scale(1);
}

.form-check label input:disabled + .input-helper:before {
	border-color: #f3f3f3;
}

.form-check label input:disabled:checked + .input-helper:after {
	color: #f3f3f3;
}

.form-check label .input-helper:before {
	position: absolute;
	content: "";
	top: 2px;
	width: 18px;
	height: 18px;
	border-radius: 2px;
	left: 0;
	border: 2px solid #f3f3f3;
	transition: all;
	transition-duration: 0s;
	transition-duration: 250ms;
}

.rtl .form-check label .input-helper:before {
	left: auto;
	right: 0;
}

.form-check label .input-helper:after {
	transition: all;
	transition-duration: 0s;
	transition-duration: 250ms;
	font-family: Material Design Icons;
	opacity: 0;
	filter: alpha(opacity=0);
	transform: scale(0);
	content: "\F12C";
	position: absolute;
	font-size: 0.9375rem;
	font-weight: bold;
	left: 0;
	top: 2px;
	color: #ab8ce4;
}

.rtl .form-check label .input-helper:after {
	left: auto;
	right: 0;
}

