/* Pixel loader */
.pixel-loader {
	width: 10px;
	height: 10px;
	background: #ffb463;
	color: #ffb463;
	margin: 80px auto;
	box-shadow: 15px 15px 0 0, -15px -15px 0 0, 15px -15px 0 0, -15px 15px 0 0,
		0 15px 0 0, 15px 0 0 0, -15px 0 0 0, 0 -15px 0 0;
	-webkit-animation: anim 2s linear infinite;
	        animation: anim 2s linear infinite;
}

@-webkit-keyframes anim {
	0% {
		-webkit-filter: hue-rotate(0deg);
	}
	50% {
		box-shadow: 20px 20px 0 0, -20px -20px 0 0, 20px -20px 0 0,
			-20px 20px 0 0, 0 10px 0 0, 10px 0 0 0, -10px 0 0 0, 0 -10px 0 0;
	}
	75% {
		box-shadow: 20px 20px 0 0, -20px -20px 0 0, 20px -20px 0 0,
			-20px 20px 0 0, 0 10px 0 0, 10px 0 0 0, -10px 0 0 0, 0 -10px 0 0;
	}
	100% {
		transform: rotate(360deg);
		-webkit-filter: hue-rotate(360deg);
	}
}

@keyframes anim {
	0% {
		-webkit-filter: hue-rotate(0deg);
	}
	50% {
		box-shadow: 20px 20px 0 0, -20px -20px 0 0, 20px -20px 0 0,
			-20px 20px 0 0, 0 10px 0 0, 10px 0 0 0, -10px 0 0 0, 0 -10px 0 0;
	}
	75% {
		box-shadow: 20px 20px 0 0, -20px -20px 0 0, 20px -20px 0 0,
			-20px 20px 0 0, 0 10px 0 0, 10px 0 0 0, -10px 0 0 0, 0 -10px 0 0;
	}
	100% {
		transform: rotate(360deg);
		-webkit-filter: hue-rotate(360deg);
	}
}

/* Square box loader */
@-webkit-keyframes dyinglight {
	15% {
		transform: scale(1.6);
	}
	50% {
		transform: rotate(-89deg);
	}
	100% {
		transform: rotate(-90deg);
	}
}
@keyframes dyinglight {
	15% {
		transform: scale(1.6);
	}
	50% {
		transform: rotate(-89deg);
	}
	100% {
		transform: rotate(-90deg);
	}
}

.square-box-loader {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	display: inline-block;
}

.square-box-loader-square {
	display: block;
	width: 30px;
	height: 30px;
	background: #03a9f3;
}

.square-box-loader-container {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	transform-origin: 50% 50% 0;
	-webkit-animation: dyinglight 1s ease infinite;
	        animation: dyinglight 1s ease infinite;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

.square-box-loader-corner-top:before,
.square-box-loader-corner-top:after,
.square-box-loader-corner-bottom:before,
.square-box-loader-corner-bottom:after {
	position: absolute;
	width: 10px;
	height: 10px;
	color: #03a9f3;
	content: "";
}

.square-box-loader-corner-top:before {
	border-left: 1px solid;
	border-top: 1px solid;
	top: -4px;
	left: -4px;
}

.square-box-loader-corner-top:after {
	border-right: 1px solid;
	border-top: 1px solid;
	top: -4px;
	right: -4px;
}

.square-box-loader-corner-bottom:before {
	border-left: 1px solid;
	border-bottom: 1px solid;
	bottom: -4px;
	left: -4px;
}

.square-box-loader-corner-bottom:after {
	border-right: 1px solid;
	border-bottom: 1px solid;
	bottom: -4px;
	right: -4px;
