/* Circle loader */
.circle-loader {
	width: 70px;
	height: 70px;
	border-radius: 100%;
	position: relative;
	margin: 0 auto;
}

.circle-loader:before,
.circle-loader:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border-radius: 100%;
	border: 10px solid transparent;
	border-top-color: #ffb463;
}

.circle-loader:before {
	z-index: 100;
	-webkit-animation: spin 1s infinite;
	        animation: spin 1s infinite;
}

.circle-loader:after {
	border: 10px solid #f4f4f4;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

/* Jumping dots loader */
.jumping-dots-loader {
	width: 100px;
	height: 100px;
	border-radius: 100%;
	position: relative;
	margin: 0 auto;
}

.jumping-dots-loader span {
	display: inline-block;
	width: 20px;
	height: 20px;
	border-radius: 100%;
	background-color: rgba(251, 150, 120, 0.8);
	margin: 35px 5px;
}

.jumping-dots-loader span:nth-child(1) {
	-webkit-animation: bounce 1s ease-in-out infinite;
	        animation: bounce 1s ease-in-out infinite;
}

.jumping-dots-loader span:nth-child(2) {
	-webkit-animation: bounce 1s ease-in-out 0.33s infinite;
	        animation: bounce 1s ease-in-out 0.33s infinite;
}

.jumping-dots-loader span:nth-child(3) {
	-webkit-animation: bounce 1s ease-in-out 0.66s infinite;
	        animation: bounce 1s ease-in-out 0.66s infinite;
}

@keyframes bounce {
	0%,
	75%,
	100% {
		transform: translateY(0);
	}
	25% {
		transform: translateY(-20px);
	}
}

/* Bar loader */
.bar-loader {
	width: 50px;
	height: 50px;
	border-radius: 100%;
	position: relative;
	margin: 0 auto;
}

.bar-loader span {
	display: inline-block;
	width: 5px;
	height: 30px;
	background-color: rgba(251, 150, 120, 0.7);
}

.bar-loader span:nth-child(1) {
	-webkit-animation: grow 1s ease-in-out infinite;
	        animation: grow 1s ease-in-out infinite;
}

.bar-loader span:nth-child(2) {
	-webkit-animation: grow 1s ease-in-out 0.15s infinite;
	        animation: grow 1s ease-in-out 0.15s infinite;
}

.bar-loader span:nth-child(3) {
	-webkit-animation: grow 1s ease-in-out 0.3s infinite;
	        animation: grow 1s ease-in-out 0.3s infinite;
}

.bar-loader span:nth-child(4) {
	-webkit-animation: grow 1s ease-in-out 0.45s infinite;
	        animation: grow 1s ease-in-out 0.45s infinite;
}

@-webkit-keyframes grow {
	0%,
	100% {
		transform: scaleY(1);
	}
	50% {
		transform: scaleY(1.8);
	}
}

@keyframes grow {
	0%,
	100% {
		transform: scaleY(1);
	}
	50% {
		transform: scaleY(1.8);
	}
}

/* Square path loader */
.square-path-loader {
	width: 100px;
	height: 100px;
	border-radius: 100%;
	position: relative;
	margin: 0 auto;
}

.square-path-loader:before,
.square-path-loader:after {
	content: "";
	width: 15px;
	height: 15px;
	position: absolute;
	top: 0;
	left: calc(50% - 10px);
	background-color: rgba(0, 194, 146, 0.7);
	-webkit-animation: squaremove 1s ease-in-out infinite;
	        animation: squaremove 1s ease-in-out infinite;
}

.square-path-loader:after {
	bottom: 0;
	-webkit-animation-delay: 0.5s;
	        animation-delay: 0.5s;
}

@-webkit-keyframes squaremove {
	0%,
	100% {
		transform: translate(0, 0) rotate(0);
	}
	25% {
		transform: translate(40px, 40px) rotate(45deg);
	}
	50% {
		transform: translate(0px, 80px) rotate(0deg);
	}
	75% {
		transform: translate(-40px, 40px) rotate(45deg);
	}
}

@keyframes squaremove {
	0%,
	100% {
		transform: translate(0, 0) rotate(0);
	}
	25% {
		transform: translate(40px, 40px) rotate(45deg);
	}
	50% {
		transform: translate(0px, 80px) rotate(0deg);
	}
	75% {
		transform: translate(-40px, 40px) rotate(45deg);
	}
}

