	height: 100px;
	border-radius: 100%;
	position: relative;
	margin: 0 auto;
}

.moving-square-loader:before {
	content: "";
	position: absolute;
	width: 10px;
	height: 10px;
	top: calc(50% - 10px);
	left: 0px;
	background-color: rgba(251, 150, 120, 0.7);
	-webkit-animation: rotatemove 1s infinite;
	        animation: rotatemove 1s infinite;
}

@-webkit-keyframes rotatemove {
