/* Avgrund Popup */
.avgrund-popin {
	position: absolute;
	background: #fff;
	padding: 37px 33px;
	overflow: hidden;
	visibility: hidden;
	opacity: 0;
	filter: alpha(opacity=0);
	top: 50%;
	left: 50%;
	z-index: 1000;
	font-size: 0.875rem;
	font-family: "Roboto", sans-serif;
	font-weight: normal;
	font-weight: initial;
	line-height: 1.85;
	border-radius: 10px;
	transform: scale(0.8);
}

.avgrund-popin p {
	font-size: 0.875rem;
	font-family: "Roboto", sans-serif;
	font-weight: normal;
	font-weight: initial;
}

.avgrund-overlay {
	background: #000;
	width: 100%;
	position: fixed;
	top: 0px;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: 101;
	visibility: hidden;
	opacity: 0;
	filter: alpha(opacity=0);
}

body.avgrund-ready,
.avgrund-ready .avgrund-popin,
.avgrund-ready .avgrund-overlay {
	transform-origin: 50% 50%;
	transition: 0.3s all ease-out;
}

body.avgrund-active {
	overflow: hidden;
}

.avgrund-active .avgrund-popin {
	visibility: visible;
	opacity: 1;
	filter: alpha(opacity=100);
	transform: scale(1.1);
}

.avgrund-active .avgrund-overlay {
	visibility: visible;
	opacity: 0.5;
	filter: alpha(opacity=50);
	height: 20000px;
}

.avgrund-popin.stack {
	transform: scale(1.5);
}

.avgrund-active .avgrund-popin.stack {
	transform: scale(1.1);
}

/* Optional close button styles */
.avgrund-close {
	display: block;
	color: #fff;
	background: #12131a;
	font-size: 10px;
	text-decoration: none;
	text-transform: uppercase;
	position: absolute;
	top: 10px;
	right: 10px;
	height: 17px;
	width: 17px;
	text-align: center;
	line-height: 16px;
	border-radius: 50px;
}

.avgrund-close:hover {
	color: #fff;
	text-decoration: none;
}

