	position: relative;
	background: #ffffff;
	border-radius: 100%;
	border: 1px solid #f3f3f3;
	width: 18px;
	height: 18px;
	top: -7px;
	outline: none;
	transition-duration: 0.4s;
}

.noUi-target .noUi-base .noUi-origin .noUi-handle:after,
.noUi-target .noUi-base .noUi-origin .noUi-handle:before {
	display: none;
}

.noUi-target .noUi-base .noUi-origin .noUi-handle:after {
	display: block;
	position: absolute;
	top: 5px;
	left: 0;
	right: 0;
	margin: auto;
	width: 6px;
	height: 6px;
	background: #03a9f3;
	border-radius: 100%;
}

