	border-bottom: 0 none;
	padding: 0 0 1px 0;
	margin-top: 0;
}

.tabcontrol > .steps > ul > li.current > a {
	padding: 15px 30px 10px 30px;
}

.tabcontrol > .content {
	position: relative;
	display: inline-block;
	width: 100%;
	height: 35em;
	overflow: hidden;
	border-top: 1px solid #f3f3f3;
	padding-top: 20px;
}

.tabcontrol > .content > .title {
	position: absolute;
	left: -999em;
}

.tabcontrol > .content > .body {
	float: left;
	position: absolute;
	width: 95%;
	height: 95%;
	padding: 2.5%;
}

.tabcontrol > .content > .body ul {
	list-style: disc;
}

.tabcontrol > .content > .body ul > li {
	display: list-item;
}

